export default class Blog extends Component {
  //const [likeheart1, setHeart1] = useState("hearto");
  //const [likeheart2, setHeart2] = useState("heart");
  // const [likebookmark1, setBookMark1] = useState("bookmark-border");
  //const [likebookmark2, setBookMark2] = useState("bookmark");

  constructor(props) {
    super(props);
    this.handleLike = this.handleLike.bind(this);
    this.handleBookmark = this.handleBookmark.bind(this);
  }
  state = {
    data: [
      { id: 1, name: "mr.abhishek_n" },
      { id: 2, name: "mr.Anjan_o" },
      { id: 3, name: "mr.Rajeev_s" },
      { id: 4, name: "navi_ks" },
      { id: 5, name: "4mr_ms" },
      { id: 6, name: "mr.abhishek_n" },
      { id: 7, name: "mr.demonblack" },
    ],
    isLoading: true,
    likeheart: "hearto",
    likebookmark: "bookmark-border",
    liked: true,
    save: true,
    stories: [
      { id: 1, name: " mr.abhishekn_n" },
      { id: 2, name: "mr.Anjan_o" },
      { id: 3, name: "mr.Rajeev_s" },
      { id: 4, name: "navi_ks" },
      { id: 5, name: "4mr_ms" },
      { id: 6, name: "mr.abhishek_n" },
      { id: 7, name: "mr.demonblack" },
    ],
    modalVisible: false,
  };
  handleLike = () => {
    this.setState({ liked: !this.state.liked });
  };
  handleBookmark = () => {
    this.setState({ save: !this.state.save });
  };
  componentDidMount() {
    axios.get(``).then((res) => {
      const persons = res.data;
    });
  }

  render() {
    return (
      <View style={{ flex: 1 }}>
        <ScrollView
          horizontal={true}
          style={{
            width: "100%",
            height: 110,
            backgroundColor: "white",
          }}
          showsHorizontalScrollIndicator={false}
        >
          <View
            style={{
              marginTop: 5,
              flexDirection: "column",
            }}
          >
            <AddStories title={"You"} />
          </View>
          <View style={{ marginTop: 5, flexDirection: "row" }}>
            {this.state.stories.map((storie) => (
              <View key={storie.id}>
                <Stories name={storie.name} />
              </View>
            ))}
          </View>
        </ScrollView>
        <ScrollView
          style={{
            flexDirection: "column",
            height: "100%",
          }}
        >
          {this.state.data.map((posts) => (
            <View style={{}} key={posts["id"]}>
              <View
                style={{
                  flexDirection: "row",
                  alignItems: "center",
                  height: 50,
                  backgroundColor: "white",
                }}
              >
                <MaterialIcons
                  name="account-circle"
                  size={28}
                  style={{ color: "black", alignSelf: "center", marginLeft: 9 }}
                />
                <Text style={{ fontSize: 15, marginLeft: 7 }}>
                  {posts.name}
                </Text>
                <MaterialIcons
                  name="dots-vertical"
                  size={28}
                  style={{
                    color: "black",
                    alignSelf: "center",
                    marginLeft: "auto",
                    marginRight: 5,
                  }}
                  onPress={() => {
                    this.setState({ modalVisible: true });
                  }}
                />
              </View>
              <Card
                //  title={posts["title"]}
                imageWrapperStyle={{
                  margin: 0,
                  padding: 0,
                }}
                titleStyle={{
                  textAlign: "left",
                  marginLeft: 8,
                  marginTop: 3,
                  marginBottom: 3,
                }}
                containerStyle={{
                  margin: 0,
                }}
                imageProps={{ resizeMode: "stretch" }}
                image={{
                  uri: "",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    margin: 0,
                  }}
                >
                  <TouchableRipple
                    onPress={() => {
                      this.handleLike();
                    }}
                    centered={true}
                    style={{ paddingLeft: 6, borderRadius: 40 }}
                    borderless={true}
                    rippleColor="rgba(0, 0, 0, .32)"
                  >
                    <Like liked={this.state.liked} />
                  </TouchableRipple>
                  <TouchableRipple
                    onPress={() => {
                      this.props.navigation.navigate("comment");
                    }}
                    centered={true}
                    style={{ paddingLeft: 6, borderRadius: 40 }}
                    borderless={true}
                    rippleColor="rgba(0, 0, 0, .32)"
                  >
                    <Icon
                      name="comment"
                      size={30}
                      style={{ margin: 4, alignSelf: "center" }}
                      onPress={() => {
                        this.props.navigation.navigate("Comment");
                      }}
                    />
                  </TouchableRipple>
                  <TouchableRipple
                    onPress={() => {
                      this.props.navigation.navigate("Direct");
                    }}
                    centered={true}
                    style={{
                      paddingLeft: 6,
                      borderRadius: 40,
                      marginRight: "auto",
                      alignSelf: "center",
                    }}
                    borderless={true}
                    rippleColor="rgba(0, 0, 0, .32)"
                  >
                    <Icon
                      name="sc-telegram"
                      size={32}
                      style={{ marginRight: "auto", alignSelf: "center" }}
                    />
                  </TouchableRipple>
                  <TouchableRipple
                    onPress={() => {
                      this.handleBookmark();
                    }}
                    centered={true}
                    style={{ paddingLeft: 6, borderRadius: 40 }}
                    borderless={true}
                    rippleColor="rgba(0, 0, 0, .32)"
                  >
                    <Bookmark save={this.state.save} />
                  </TouchableRipple>
                </View>
              </Card>
            </View>
          ))}
        </ScrollView>
      </View>
    );
  }
}
