import React from "react";
import Icon from "react-native-vector-icons/Ionicons";
import {
  View,
  Text,
  Image,
  Dimensions,
  Linking,
  TouchableOpacity,
} from "react-native";
import MaterialIcon from "react-native-vector-icons/MaterialCommunityIcons";
import IonIcon from "react-native-vector-icons/Ionicons";
const AddStories = (props) => {
  function getheight() {
    if (props.height > 0) {
      return props.height;
    } else {
      return 70;
    }
  }
  function getwidth() {
    if (props.height > 0) {
      return props.width;
    } else {
      return 70;
    }
  }
  function getTop() {
    if (props.top > 0) {
      return props.top;
    } else {
      return 40;
    }
  }
  function getLeft() {
    if (props.left > 0) {
      return props.left;
    } else {
      return 50;
    }
  }
  function getText() {
    if (props.title !== "undefined") {
      return props.title;
    } else {
      return "You";
    }
  }
  function getTextStyle() {
    if (props.style !== "undefined") {
      return props.style;
    } else {
      return { fontSize: 10 };
    }
  }
  console.log(props.height);
  return (
    <View
      style={{
        marginLeft: 5,
        alignItems: "center",
      }}
    >
      <View
        style={{
          alignItems: "center",
        }}
      >
        <View
          style={{
            borderRadius: 250,
            width: getwidth(),
            height: getheight(),
            justifyContent: "center",
            alignItems: "center",
            overflow: "hidden",
          }}
        >
          <Image
            resizeMode="cover"
            source={require("../../assets/img1.jpg")}
            style={{
              width: "100%",
              height: "100%",
              flex: 1,
              borderRadius: 30,
              overflow: "hidden",
            }}
          />
        </View>
        <Icon
          name="md-add-circle"
          size={28}
          style={{
            position: "absolute",
            color: "blue",
            top: getTop(),
            left: getLeft(),
          }}
        />
      </View>
      <Text style={getTextStyle()}>{getText()}</Text>
    </View>
  );
};

export default AddStories;
