import React from "react";
import {
  View,
  Text,
  Image,
  Dimensions,
  Linking,
  TouchableOpacity,
} from "react-native";
import MaterialIcon from "react-native-vector-icons/MaterialCommunityIcons";
import IonIcon from "react-native-vector-icons/Ionicons";
const Stories = (props) => {
  return (
    <View
      style={{
        marginLeft: 5,

        alignItems: "center",
      }}
    >
      <View
        style={{
          alignItems: "center",
        }}
      >
        <View
          style={{
            borderRadius: 250,
            width: 70,
            height: 70,
            justifyContent: "center",
            alignItems: "center",
            overflow: "hidden",
            borderWidth: 2,
            borderColor: "#F01074",
          }}
        >
          <Image
            resizeMode="cover"
            source={require("../../assets/img2.jpg")}
            style={{
              width: "100%",
              height: "100%",
              flex: 1,
              borderRadius: 30,
              overflow: "hidden",
            }}
          />
        </View>
      </View>
      <Text style={{ fontSize: 10 }}>{props.name}</Text>
    </View>
  );
};

export default Stories;
