import * as React from "react";
import { Component } from "react";
import AntIcon from "react-native-vector-icons/AntDesign";
class Like extends Component {
  //  state = { :  }
  render() {
    let classes = "hearto";
    let color = "black";
    if (!this.props.liked) {
      classes = "heart";
      color = "red";
    }
    return (
      <AntIcon
        name={classes}
        size={23}
        color={color}
        style={{
          marginTop: 8,
          marginLeft: 1,
          marginRight: 8,
          alignSelf: "center",
        }}
      />
    );
  }
}

export default Like;
