import { createMaterialBottomTabNavigator } from "@react-navigation/material-bottom-tabs";
import Comment from "./comments";
import * as React from "react";
import { View, Text, ScrollView, Dimensions, Image } from "react-native";
import { Header } from "react-native-elements";
import MyPosts from "./TopNavigator/MyPosts";
import { FlatList, TouchableOpacity } from "react-native-gesture-handler";
import { Camera } from "expo-camera";
import * as Permissions from "expo-permissions";
import MaterialIcon from "react-native-vector-icons/MaterialCommunityIcons";

const Tab = createMaterialBottomTabNavigator();

export default function GlobalTab({ navigation }) {
  const [hasPermission, setHasPermission] = React.useState(null);
  const [type, setType] = React.useState(Camera.Constants.Type.back);
  const [recording, startRecording] = React.useState("");

  React.useEffect(() => {
    (async () => {
      const { status } = await Permissions.getAsync(Permissions.CAMERA);
      setHasPermission(status === "granted");
    })();
  }, []);
  if (hasPermission === null) {
    return (
      <View>
        <Text>Please allow Camera Permission</Text>
      </View>
    );
  }
  if (hasPermission === false) {
    Permissions.getAsync(Permissions.CAMERA);
  }
  function record() {
    recording === "" ? startRecording("REC") : startRecording("");
    console.log("recording");
  }
  async function takesnap() {}

  return (
    <View style={{ flex: 1 }}>
      <Camera style={{ flex: 1 }} type={type}>
        <Text
          style={{
            color: "red",
            fontSize: 20,
            position: "absolute",
            fontWeight: "700",
            top: 30,
            left: 10,
          }}
        >
          {recording}
        </Text>
      </Camera>
      <View
        style={{
          flex: 0.5,
          backgroundColor: "transparent",
          flexDirection: "row",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <View
          style={{
            flex: 1,
            flexDirection: "row",
            justifyContent: "space-evenly",
            alignItems: "center",
          }}
        >
          <TouchableOpacity
            onPress={() => {
              setType(
                type === Camera.Constants.Type.back
                  ? Camera.Constants.Type.front
                  : Camera.Constants.Type.back
              );
            }}
          >
            <MaterialIcon name="rotate-3d" size={35} />
          </TouchableOpacity>
          <TouchableOpacity
            style={{
              width: 100,
              height: 100,
            }}
            onPress={() => {
              takesnap();
            }}
          >
            <View
              style={{
                width: "100%",
                height: "100%",
                alignSelf: "center",
                justifyContent: "center",
              }}
            >
              <View
                style={{
                  borderRadius: 200,
                  width: "80%",
                  height: "80%",
                  alignSelf: "center",
                  justifyContent: "center",
                  borderWidth: 15,
                  borderColor: "grey",
                }}
              >
                <View
                  style={{
                    width: "30%",
                    height: "30%",
                    alignSelf: "center",
                    justifyContent: "center",
                  }}
                ></View>
              </View>
            </View>
          </TouchableOpacity>
          <TouchableOpacity>
            <MaterialIcon
              name={recording === "" ? "video-outline" : "pause"}
              size={42}
              onPress={() => {
                record();
              }}
            />
          </TouchableOpacity>
        </View>
      </View>
    </View>
  );
}
