import { createMaterialBottomTabNavigator } from "@react-navigation/material-bottom-tabs";
import Icon from "react-native-vector-icons/FontAwesome";
import MaterialIcon from "react-native-vector-icons/MaterialCommunityIcons";
import Feather from "react-native-vector-icons/Feather";
import FeatherIcons from "react-native-vector-icons/Feather";
import EvilIcon from "react-native-vector-icons/EvilIcons";
import { TransitionPresets } from "@react-navigation/stack";
import Blog from "./Blog";
import * as React from "react";
import Comment from "./comments";
import HomeTab from "./hometab";
import SearchTab from "./SearchTab";
import ActivityTab from "./ActivityTab";
import AccountTab from "./AccountTab";
import GlobalTab from "./GlobalTab";
import { Alert, Platform } from "react-native";
import { createDrawerNavigator } from "@react-navigation/drawer";
import { NavigationContainer } from "@react-navigation/native";
import { createStackNavigator, HeaderTitle } from "@react-navigation/stack";
import { Header } from "react-native/Libraries/NewAppScreen";
import Camera from "./hardware/Camera";
import AntIcon from "react-native-vector-icons/AntDesign";
import FontawesomeIcon from "react-native-vector-icons/FontAwesome";
import { black } from "react-native-paper/lib/typescript/src/styles/colors";
import MenuModal from "./MenuModal";
import Direct from "./direct";
import VideoEdit from "./VideoEdit";
const Tab = createMaterialBottomTabNavigator();
const HomeStack = createStackNavigator();
const SearchStack = createStackNavigator();
const GlobalStack = createStackNavigator();
const ActivityStack = createStackNavigator();
const AccountStack = createStackNavigator();
export default function Home() {
  function getheaderStyle() {
    if (Platform.OS === "android") {
      return "fontFamily:'Roboto'";
    } else {
      return;
    }
  }
  function HomeStackScreen(props) {
    return (
      <HomeStack.Navigator>
        <HomeStack.Screen
          name="Home"
          component={Blog}
          options={{
            headerTitle: "Instagram",

            ...TransitionPresets.RevealFromBottomAndroid,
            headerLeft: () => (
              <FeatherIcons
                name="camera"
                size={28}
                onPress={() => {
                  props.navigation.push("Camera");
                }}
                style={{
                  marginLeft: 8,
                  alignSelf: "center",
                  marginRight: 0,
                  paddingRight: 0,
                }}
              />
            ),
            headerRight: () => (
              <FeatherIcons
                name="send"
                size={28}
                onPress={() => {
                  props.navigation.push("Direct");
                }}
                style={{ marginRight: 6, marginTop: 4 }}
              />
            ),
          }}
        />
        <HomeStack.Screen
          name="Comment"
          component={Comment}
          options={{ headerTitle: "Comments" }}
        />
        <HomeStack.Screen
          name="MenuModal"
          component={MenuModal}
          options={{ headerShown: false }}
        />
        <HomeStack.Screen
          name="Direct"
          component={Direct}
          options={{ headerShown: false }}
        />
        <HomeStack.Screen
          name="openVideo"
          component={GlobalTab}
          options={{ headerShown: false }}
        />
        <HomeStack.Screen
          name="Video"
          component={VideoEdit}
          options={{ headerShown: false }}
        />
        <HomeStack.Screen
          name="message"
          component={GlobalTab}
          options={{ headerShown: false }}
        />
      </HomeStack.Navigator>
    );
  }

  const [likeheart1, setHeart1] = React.useState("hearto");
  const [likeheart2, setHeart2] = React.useState("heart");
  return (
    <NavigationContainer independent={true}>
      <Tab.Navigator
        initialRouteName="Home"
        labeled={false}
        backBehavior="history"
        sceneAnimationEnabled={false}
        barStyle={{
          margin: 0,
          backgroundColor: "white",
        }}
        screenOptions={({ route }) => ({
          tabBarIcon: ({ focused, color }) => {
            let iconName = "";
            let tag;
            if (route.name === "Activity") {
              iconName = focused ? "heart" : "hearto";
              return <AntIcon name={iconName} size={24} />;
            } else if (route.name === "Home") {
              iconName = focused ? "home" : "home-outline";
              return (
                <MaterialIcon
                  name={iconName}
                  size={26}
                  color={color}
                  style={{ color: "black" }}
                />
              );
            } else if (route.name === "Account") {
              iconName = focused ? "account-circle" : "account-circle-outline";
              return (
                <MaterialIcon
                  name={iconName}
                  size={24}
                  color={color}
                  style={{ color: "black" }}
                />
              );
            } else if (route.name === "Search") {
              iconName = focused ? "search" : "search";
              let styles;
              if (focused) {
                return <FontawesomeIcon name="search" size={25} />;
                alert("focused");
              } else
                return (
                  <Feather
                    name={iconName}
                    size={25}
                    style={{ fontWeight: "900" }}
                  />
                );
            }
          },
        })}
      >
        <Tab.Screen
          name="Home"
          component={HomeStackScreen}
          options={{
            tabBarLabel: "Home",
          }}
        />
        <Tab.Screen
          name="Search"
          component={SearchTab}
          options={{
            tabBarLabel: "Search",
            tabBarIcon: ({ color }) => <Feather name="search" size={25} />,
          }}
        />
        <Tab.Screen
          name="Global"
          options={{
            tabBarLabel: "Global",
            ...TransitionPresets.FadeFromBottomAndroid,
            tabBarIcon: ({ color }) => <Icon name="plus-square-o" size={30} />,
          }}
          component={GlobalTab}
        />
        <Tab.Screen name="Activity" component={ActivityTab} options={{}} />
        <Tab.Screen name="Account" component={AccountTab} />
      </Tab.Navigator>
    </NavigationContainer>
  );
}
